.nav {
    grid-area: n;
    background-color: brown;
    padding: 10px;
    box-shadow: inset -6px -6px 12px rgba(0, 0, 0, 0.8), inset 6px 6px 12px rgba(255, 255, 255, 0.4);
}

.item a {
    color: white;
    text-decoration: none;
    font-size: 20px;
}

.item a:hover {
    color: black;
}

.item a.activeLink {
    color: yellow;
}