.header {
    grid-area: h;
    background-color: black;
    box-shadow: inset -6px -6px 12px rgba(0, 0, 0, 0.8), inset 6px 6px 12px rgba(255, 255, 255, 0.4);
}

.header img {
    width: 40px;
    padding:10px;
    background-color:yellow;
}

.loginBlock{
    float: right;
    color: white;
    padding:10px;
    margin:10px;
    font-size: 24px;
    font-family: 'Montserrat', sans-serif;
}

.loginBlock a {
    text-decoration: none;
    color: white;
}

.loginBlock a:hover,.logoutBtn button:hover {
    opacity: 0.8;
}
  
.loginBlock .logoutBtn button {
    border: none;
    font-size: 24px;
    background-color:red;
    font-weight: bold;
    font-family: 'Montserrat', sans-serif;
    color:white;
}



