.footerContainer {
    text-align: center;
    padding: 10px;
    box-shadow: inset -6px -6px 12px rgba(0, 0, 0, 0.8), inset 6px 6px 12px rgba(255, 255, 255, 0.4);
}

.footerContainer a, p {
    text-decoration: none;
    color: white;
}

.footerContainer a:hover {
    text-decoration: underline;
    color: yellow;
}
