.cPlayer {
    display: block;
    background-color: gray;
    display: block;
    margin: 0px auto;
    padding: 50px;
    min-height: 87vh;
    box-shadow: inset -6px -6px 12px rgba(0, 0, 0, 0.8), inset 6px 6px 12px rgba(255, 255, 255, 0.4);
}

.cPlayer > h4 {
    color: #FFF;
    text-shadow: 2px 2px 4px rgba(0,0,0,0.8), -2px -2px 4px rgba(255,255,255,0.4);
    font-size: 18px;
    text-transform: uppercase;
    font-weight: 500;
    text-align: center;
}

.cPlayer > p {
    color: #EEE;
    font-size: 14px;
    text-align: center;
    font-weight: 600;
}

.cPlayer > p span {
    font-weight: 400;
}
