.cPlayerControls {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
}

.playBtn {
    display: flex;
    margin: 0 30px;
    padding: 20px;
    border-radius: 50%;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.8), -4px -4px 10px rgba(255, 255, 255, 0.4), inset -4px -4px 10px rgba(0, 0, 0, 0.4), inset 4px 4px 10px rgba(255, 255, 255, 0.4);
    border: none;
    outline: none;
    background-color: #FFCE00;
    color: #FFF;
    font-size: 24px;
    cursor: pointer;
}

.skipBtn {
    background: none;
    border: none;
    outline: none;
    cursor: pointer;
    color: #EEE;
    font-size: 18px;
}
