.paginator {
    padding: 50px 0px 0px;
    text-align: center;
}

.paginator button {
    margin: 10px;
    height: 40px;
    width: 80px;
    color: white;
    background-color: black;
    border: none;
}

.paginator button:hover {
    opacity: 0.8;
}

.pageNumber {
    padding: 8px;
    border: 2px solid black;
    font-size: 18px;
}

.pageNumber.selectedPage{
    font-weight: bold;
    border-color:gray;
}



