.getLoginFormBtn button{
    position: fixed;
    background-color: #4CAF50;
    color: white;
    top: 50%;
    left: 50%;
    width: 200px;
    height: 80px;
    border: none;
    cursor: pointer;
    font-family: 'Montserrat', sans-serif;
    font-size: 20px;
}

.getLoginFormBtn button:hover {
    opacity: 0.8;
}