.loginForm{
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #FFF;
    padding: 50px;
    font-family: 'Montserrat', sans-serif;
    margin: 30px;
}

.loginForm h1{
    text-align: center;
}
.rememberMeBlock span{
    display: inline-block;
}
.emailBlock, .passwordBlock, .loginBtn, .rememberMeBlock{
    padding: 10px 20px;
}

.emailBlock input, .passwordBlock input{
    width: 100%;
    padding: 12px 20px;
    display: inline-block;
    box-sizing: border-box;
    font-size: 14px;
}

.loginBtn button {
    text-align: center;
    background-color: #4CAF50;
    color: white;
    padding: 14px 20px;
    border: none;
    cursor: pointer;
    font-family: 'Montserrat', sans-serif;
    width: 100%;
}

.loginBtn button:hover {
    opacity: 0.8;
}
