.photo {
    width: 100px;
    border-radius: 20px;
}

.photo, .user, .userName{
    text-align: center;
}

.user {
    padding: 10px;
}

.followBtn button, .unfollowBtn button {
    font-family: 'Montserrat', sans-serif;
    padding: 1px 5px;
    color: white;
    font-size: 26px;
    border: none;
}

.followBtn button {
    background-color: #4CAF50;
}

.unfollowBtn button {
    background-color: red;
}

.followBtn button:hover, .unfollowBtn button:hover {
    opacity: 0.8;
}

.userName {
    padding: 5px;
}

.userName a {
    text-decoration:none;
    font-size: 24px;
    color: black;
}

.userName a:hover {
    color: burlywood;
}

